.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f8e2c9;
  text-align: center;
}

.home-container h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.home-container p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.start-button {
  padding: 10px 20px;
  font-size: 1.2rem;
  cursor: pointer;
  background-color: #e67e22;
  color: white;
  border: none;
  border-radius: 5px;
}

.start-button:hover {
  background-color: #d35400;
}